.choice-item {
    display: flex;
    justify-content: center;
}

.choice-item__lable {
    position: relative;
    text-transform: uppercase;
    font-size: 18px;
    line-height: 21px;
    font-weight: 700;
}

.choice-item__radio:checked  + .choice-item__lable {
    color: $primary-yellow;
}

.choice-item__radio:checked  + .choice-item__lable::after {
    content: "";
    position: absolute;
    top: 25px;
    display: block;
    width: 100%;
    height: 3px;
    border-radius: 3px;
    background-color: $primary-yellow;
}