.header {
    display: flex;
    align-items: center;
    padding-left: 20px;
    padding-right: 20px;
}

.header__logo {
    display: flex;
    width: 48px;
    height: 48px;
    border-radius: 9px;
    background-color: $primary-yellow;
    overflow: hidden;
}

.header__logo-icon {
    width: 25px;
    height: 29px;
    margin: auto;
}

.header__title {
    margin: 0;
    margin-left: 12px;
    padding: 0;
    padding-right: 5px;
    font-weight: 700;
    font-size: 20px;
    line-height: 24px;
}